
.stars {
  display: flex;
}

.stars__star {
  margin-right: 4px;
  height: 16px;
  width: 16px;
  background-image: url(/images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.card__rewiews .stars--0 .stars__star:nth-child(0),
.card__rewiews .stars--1 .stars__star:nth-child(-n+1),
.card__rewiews .stars--2 .stars__star:nth-child(-n+2),
.card__rewiews .stars--3 .stars__star:nth-child(-n+3),
.card__rewiews .stars--4 .stars__star:nth-child(-n+4),
.card__rewiews .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url(/images/star-active.svg);
}

.card__rewiews .stars .stars__star:last-child {
  margin-right: 0;
}
